<template>
  <div>
    <v-page-title vtitle="指标维护"></v-page-title>

    <el-row :gutter="20">
      <el-col :span="5">
        <div class="grid-content bg-purple">

          <v-tree></v-tree>
        </div>
      </el-col>
      <el-col :span="19">
        <div class="grid-content bg-purple">
          <div class="approval">
            <div class="app-form">
              <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="">
                  <el-input v-model="formInline.num" placeholder="指标编号"></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formInline.dec" placeholder="指标名称"></el-input>
                </el-form-item>

                <el-form-item>
                  <el-button type="primary" @click="onSubmit">查找</el-button>
                </el-form-item>
              </el-form>
            </div>
            <div class="app-table">
              <el-tree
                :data="data2"
                :props="defaultProps"
                show-checkbox
                node-key="id"
                default-expand-all
                :expand-on-click-node="false"
                :render-content="renderContent">
              </el-tree>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>

  </div>


</template>

<script>
  import axios from 'axios'
  import vPageTitle from '../../common/pageTitle.vue'
  import vTree from '../../common/eTree.vue'
  let id = 1000;
  export default {
    data() {
      return{
        formInline: {
          user: '',
          region: ''
        },
        tableData:[],
        data2: [{
          id: 1,
          label: '少数民族经济指标',
          children: [{
            id: 4,
            label: '财政',
            children: [{
              id: 9,
              label: '财政收入'
            }, {
              id: 10,
              label: '财政支出'
            }]
          }, {
            id: 2,
            label: '人口与资源',
            children: [{
              id: 7,
              label: '民族人口'
            }, {
              id: 8,
              label: '占总人口比重'
            }]
          }, {
            id: 3,
            label: '社会科学',
            children: [{
              id: 5,
              label: '卫生'
            }, {
              id: 6,
              label: '教育'
            }]
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }

      }
    },
    components: {
      vPageTitle, vTree
    },

    methods: {
      remote(row,callback){
        setTimeout(function() {
          callback(row.children)
        },500)
      },
      handleClick() {
        console.log(1);
      },
      onSubmit() {
        console.log('submit!');
      },
      append(store, data) {
        store.append({ id: id++, label: 'testtest', children: [] }, data);
      },

      remove(store, data) {
        store.remove(data);
      },

      renderContent(h, { node, data, store }) {
        return (
          <span>
          <span>
          <span>{node.label}</span>
        </span>
        <span style="float: right; margin-right: 20px">
          <el-button size="mini"  type="info" on-click={ () => this.append(store, data) }>添加下级分类</el-button>
         <el-button size="mini"  type="danger" on-click={ () => this.remove(store, data) }>删除</el-button>
        </span>
        </span>);
      }
    },

    created: function () {
      axios.get('')
        .then(response => {
          console.log(response);
        })
        .catch(err => {
          console.log(err);
        })
    }
  }
</script>
<style>
  .el-tree-node > .el-tree-node__children{
    overflow: inherit;
  }
</style>



