<template>
  <!--采集审批-->
  <div class="approval">
    <v-page-title vtitle="批量采集"></v-page-title>
    <div class="app-form">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="formInline.num" placeholder="采集编号"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formInline.dec" placeholder="采集描述"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="dialogadd = true">新增</el-button>


          <el-button type="primary" @click="onSubmit">提交</el-button>

        </el-form-item>


      </el-form>
      <el-dialog title="新增" :visible.sync="dialogadd">
        <el-form :model="form" label-width="120px">
          <el-form-item label="采集编号" >
            <el-input v-model="form.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="采集描述" >
            <el-input v-model="form.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="采集表">
            <el-select v-model="form.region2" @change="changeValue" placeholder="请选择">
              <el-option label="财政支出数据表" value="shanghai"></el-option>
              <el-option label="财政收入数据表" value="beijing"></el-option>
              <el-option label="民族人口信息表" value="wac"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="上传采集文件">
            <el-upload
              class="upload-demo"
              action="https://jsonplaceholder.typicode.com/posts/"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :file-list="fileList"
              :on-success="handlsucess">

              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-form-item>
          <el-row :gutter="20">
            <el-col :span="12"><div class="grid-content bg-purple">

              <el-form-item label="">
                <el-input type="textarea" id="texta1" value="" placeholder="采集表字段"  ></el-input>
              </el-form-item>

            </div></el-col>
            <el-col :span="12"><div class="grid-content bg-purple">

              <el-form-item label="">
                <el-input type="textarea" id="texta2" value="" placeholder="采集文件" ></el-input>
              </el-form-item>

            </div></el-col>
          </el-row>
          <el-form-item label="">
            <el-input type="textarea" id="texta3" value="" placeholder="采集文件" ></el-input>
          </el-form-item>
        </el-form>

      </el-dialog>
    </div>
    <div class="app-table">
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          prop="date"
          label="采集编号">
        </el-table-column>
        <el-table-column
          prop="name"
          label="采集描述">
        </el-table-column>
        <el-table-column
          prop="province"
          label="采集状态">
        </el-table-column>
        <el-table-column
          prop="city"
          label="采集时间">
        </el-table-column>
        <el-table-column
          prop="address"
          label="采集人">
        </el-table-column>

        <el-table-column
          fixed="right"
          label="操作"
          width="100">
          <template scope="scope">
            <el-button @click="handleClick" type="text" size="small">修改</el-button>
            <el-button type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>

</template>

<script>
  import vPageTitle from '../../common/pageTitle.vue'
  import ElForm from "../../../../node_modules/element-ui/packages/form/src/form.vue";
  export default {
    methods: {
      handleClick() {
        console.log(1);
      },
      onSubmit() {
        console.log('submit!');
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handlsucess(response){
        var oTexta = document.getElementsByClassName('el-textarea__inner');
        oTexta[1].value =this.arr1.toString().split(',')
        console.warn(response)
      },
      changeValue(value){

        var oTexta = document.getElementsByClassName('el-textarea__inner');


        if(value.toUpperCase() == 'SHANGHAI'){
          oTexta[0].value =this.arr.toString().split(',')
        }




      }
    },

    data() {
      return {
        fileList: [],
        arr:['指标名称','省份编号','省份名称','省份编号','指标值'],
        arr1:['SERIES_ID','指标名称','单位','年份','VALUE'],
        dialogadd: false,
        form: {
          name: '',
          region: '',
          region2: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px',
        formInline: {
          user: '',
          region: ''
        },
        tableData: [{
          date: 'BL2928392',
          name: '文件导入采集',
          province: '已审批',
          city: '2016-05-01',
          address: '张三'
        }, {
          date: 'BL2928393',
          name: 'Excel文件采集',
          province: '待审批',
          city: '2016-05-01',
          address: '张三'
        }, {
          date: 'BL2928395',
          name: 'GDP批量采集',
          province: '已采集',
          city: '2016-05-01',
          address: '张三'
        }]
      }
    },
    components:{
      ElForm,
      vPageTitle
    }
  }
</script>



